<script>
export default {
    data(){
        return{
            list:[
                {lj:"http://www.mei.net.cn/jcj/202409/532887551074146197.html",img:require("@/assets/xq/1.png"),id:1,title:"山东省临清工业学校与宇捷轴承举行轴承产业人才培养校企合作签约暨产教融合实习实训基地授牌仪式",time:"2024-09-23",nr:"山东省临清工业学校与宇捷轴承举行轴承产业人才培养校企合作签约暨产教融合实习实训基地授牌仪式。为培育更多轴承产业高素质技术技能人才，推动临清市经济高质量发展，9月20日，山东省临清工业学校与山东宇捷轴承制造有限公司举行轴承产业人才培养校企合作签约暨产教融合实习实训基地授牌仪式。临清市工业学校党委副书记、校长杨卫国在活动中为宇捷轴承制造有限公司授牌，市中华职教社、烟店镇政府、临清工业学校、山东宇捷轴承制造有限公司相关负责同志参加活动。"},
                {lj:"http://html.cbia.com.cn/www/Home/Infoforum/info_detail/code=ATH1741938108BLV.html",img:require("@/assets/xq/2.png"),id:2,title:"【校企合作 共育英才】—— 产教深度融合结硕果 建龙哈轴助力学子成才",time:"2025-03-14",nr:"2025年3月13日，哈尔滨轴承集团有限公司（以下简称“建龙哈轴”）迎来了河南科技大学的8名本科毕业生，他们将在建龙哈轴进行为期两个月的毕业设计课题实习。此次实习是建龙哈轴与河南科技大学校企合作的重要一环，旨在通过校企联合培养模式，帮助学生在完成毕业设计的同时，快速成长为符合企业需求的高素质人才。"},
                {lj:"https://news.zcwz.com/507067.html",img:require("@/assets/xq/3.png"),id:3,title:"聊城产教融合创新模式 助力轴承行业人才培养",time:"2024-09-27",nr:"轴承被称为工业制造的“关节”，而聊城则是中国五大轴承产业集群之一。随着产业的智能化和数字化转型，聊城轴承企业面临着对高水平技能人才的需求，特别是那些能够在操作、工艺理解、管理协调、技术创新等方面发挥作用的现场工程师。为了解决这一问题，2023年聊城市出台了一系列支持轴承产业高质量发展的措施，其中提出依托重点轴承园区建立产教融合平台，实施校企联合招生、培养、实习实训以及师资实践，旨在打造国家级别的轴承产教融合示范点。"},
                {lj:"https://www.bearing.cn/news/newsshow.html?id=108294&tp=wyxd&uid=359297",img:require("@/assets/xq/4.png"),id:4,title:"校企联动赋能创新 产教融合共育人才",time:"2025-03-24",nr:"3月21日，洛阳巨创轴承科技有限公司与洛阳理工学院智能制造学院联合开展“产教融合 深化发展”校企合作参观交流活动，进一步推动高端装备制造领域人才培养与产业需求的精准对接。在公司综合办、技术中心、生产部工作人员的陪同下，师生一行实地参观了车保车间、热处理车间、磨装车间生产现场，详细了解了轴承产品的加工工艺流程。参观结束后，综合办负责人向学生们介绍了公司目前正处于快速发展的关键阶段，急需智能制造领域的技术攻关和人才储备，洛阳理工学院智能制造学院在智能装备研发、精密加工技术等方面的优势，与公司需求高度契合，期待在技术难题攻关、人才培养等方面开展实质性合作。随后，技术中心与带队老师就非金属材料切削中遇到的问题进行了初步沟通，双方表达了合作意愿。"},
                {lj:"http://html.cbia.com.cn/www/Home/Infoforum/info_detail/code=ATH1736474060PF8T.html",img:require("@/assets/xq/5.png"),id:5,title:"西北轴承与西安交通大学校企携手，学术交流活动圆满举行",time:"2025-01-10",nr:"近日，公司技术中心研发人员与西安交通大学研究团队成功举办学术交流活动。交流会上，公司研发人员详细介绍了在轴承设计、材料选用、加工工艺以及质量检测等方面的实际操作流程和技术优势。高精度加工要求和严谨的质量管控体系，让高校专家学者们直观感受到了企业在轴承制造领域的深厚底蕴和实践成果，也为后续学术探讨提供了丰富的现实依据。西安交通大学研究团队也分享了在轴承材料微观结构分析、摩擦学性能优化、智能轴承监测技术等前沿领域的最新研究成果。深入浅出的讲解、严谨的实验数据以及大胆创新的理论设想，拓宽了公司技术人员的科研视野，为解决实际生产中遇到的技术难题提供了新的思路和方法。双方围绕轴承疲劳寿命提升、特殊工况下的轴承性能优化、绿色制造工艺创新等关键问题展开了热烈讨论，从理论研究到工程应用，每一个观点都经过深入剖析，每一个方案都经过反复斟酌，力求找到理论与实践的最佳结合点，实现科研成果向生产力的高效转化。此次学术交流活动，不仅是知识的共享，更是校企合作的深化。通过面对面沟通与交流，公司与西安交通大学在人才培养、项目合作、技术创新等方面达成了多项共识，为建立长期稳定的产学研合作关系奠定了坚实基础。下一步，双方将携手共进，充分发挥各自优势，在轴承制造这一关键领域不断深耕细作，共同攻克技术难关，推动行业技术进步。"},
                {lj:"https://news.wust.edu.cn/info/1061/392702.htm",img:require("@/assets/xq/6.png"),id:6,title:"央广网：政府“做媒” 民企与高校“联姻”",time:"2024-08-08",nr:"8月7日上午，环驰轴承集团有限公司技术总监舒建明召集公司研发、技术骨干，开了一次“碰头会”，商讨下半年新产品研发、技改推进计划。其间，他们得到武汉科技大学（以下简称“武科大”）相关专家的在线指导，明确下阶段高速轴承生产技术的攻坚方向。“环驰”数字化车间（慈溪市委宣传部供图）“环驰”是宁波轴承行业的龙头企业。今年上半年，该公司轴承总产量超过3亿套，"
                +"营业收入逾11亿元。我们携手高校、科研院所加大新产品开发力度，向新能源汽车产业领域加速进军。”舒建明说，上个月，经慈溪市委人才办牵线搭桥，“环驰”与武科大签订校企合作协议，迎来产品研发“新参谋”。武汉科技大学-环驰轴承集团有限公司创新实践基地揭牌（慈溪市委宣传部供图）创新不停顿，发展不止步，是慈溪企业高质量发展“主旋律”。今年7月以来，武科大与环驰轴承集团有限公司、浙江瑞堂塑料科技有限公司、宁波宝工电器有限公司、浙江广慈医疗器械有限公司等企业深入开展人才科技合作，构筑产学研创新合作平台，助力慈溪全面加强“三支队伍”建设，进一步激发全面深化改革的活力、动力。签约现场（慈溪市委宣传部供图）慈溪民企与武科大开展产学研合作，源于今年4月举行的2024宁波人才日慈溪专场活动。当时，慈溪市人民政府与该校签署战略合作框架协议，正式启用武汉科技大学慈溪技术转移与人才引育合作基地。据悉，双方将充分发挥各自资源优势，依托武汉科技大学慈溪技术转移与人才引育合作基地，以“专家服务团+签约教授”产学研合作赋能模式，共建一批校企联合创新中心、博士后工作站等创新载体；建立人才资源共享机制，共同推动人才的引进和培育。武汉科技大学-浙江瑞堂塑料科技有限公司校企联合创新中心揭牌（慈溪市委宣传部供图）新质生产力发展的动力在于技术和人才，人才培育的生命力在于实践和应用。今年以来，武科大专家教授通过线上交流、线下走访，为企业创新发展出谋划策，助力慈溪教育、科技、人才“三位一体”融合发展，唱响校地合作“协奏曲”。“广慈”生产车间（慈溪市委宣传部供图）浙江广慈医疗器械有限公司是一家高新技术企业。近年来，该公司“牵手”浙江大学、国科大杭州高等研究院等高校、科研院所，围绕材料创新、技术创新，做好“创新医疗器械”文章。“政府‘做媒’，校企‘联姻’，使‘广慈’在骨科植入医疗器械研发领域迎来‘强力外援’。”该公司办公室主任陈晓群表示，“广慈”将与武科大共建省级博士后工作站，在生物材料研究领域开展深入合作，提高公司的医疗器械金属材料表面涂层处理水平。"}            ],
            dialogVisible:false,
            nr:''
        }
