// src/router/index.js
import Vue from 'vue';
import VueRouter from 'vue-router';
//  import Login from "@/views/loginView.vue";
import HelloWorld from '@/components/HelloWorld.vue';
import layoutV from '@/views/hh/layoutV.vue';
import l2V from '@/views/hh/l2V.vue';
import l3V from '@/views/hh/l3V.vue';
import l4V from '@/views/hh/l4V.vue';
import l5V from '@/views/hh/l5V.vue';
import l6V from '@/views/hh/l6V.vue';
import rcxqV from '@/views/hh/rcxqV.vue';
import L7V from '@/views/hh/l7V.vue';
Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: HelloWorld
  },
  {
    path:'/hello',
    name:'hello',
    component:HelloWorld,
    children:[
        {path:'/l1',name:"l1",component:layoutV},
        {path:'/l2',name:"l2",component:l2V},
        {path:'/l3',name:"l3",component:l3V},
        {path:'/l4',name:"l4",component:l4V},
        {path:'/l5',name:"l5",component:l5V},
        {path:'/l6',name:"l5",component:l6V},
        {path:'/rcxqV',name:"l5",component:rcxqV},
        {path:'/l7',name:"l7",component:L7V},
    ]
  }
  // 定义其他路由...
];

const router = new VueRouter({
  mode: 'history', // 使用 HTML5 History 模式
  base: process.env.BASE_URL,
  routes
});
// 🔥 解决重复点击导航报错的问题
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => {
    if (err.name !== 'NavigationDuplicated') throw err;
  });
};
export default router;